import ReactLogo from '../../images/logo.svg';

const Logo = () => {
    return (
        <div className="header-top__logo">
            <a className="header-top__logo-link" href="/">
                <img 
                    className="header-top__logo-link-img"
                    src={ReactLogo} 
                    alt="logo"
                />  
            </a>         
        </div>
    )
}

export default Logo;